import { Ad, Inquiry } from '@core/models';
import { APP_ROUTE } from './route.constant';
import { AgentHubActions, EnquiryStatus } from '@core/enums/agent.enum';
import { AdStatus } from '@core/enums';

export const AD_DETAIL_ACTIONS = (ad: Ad) => [
	{
		name: 'Renew Ad',
		routerLink: `/${APP_ROUTE?.editAd}/${ad?.id}`,
		icon: 'sync',
		canCloseDialog: true,
		reference: AgentHubActions.RENEW_AD,
		textClass: 'fg-general-5 text-body-regular',
		isHidden: ![AdStatus.EXPIRED].includes(ad.adStatus.status as AdStatus),
	},
	{
		name: 'Edit Ad',
		routerLink: `/${APP_ROUTE?.editAd}/${ad?.id}`,
		icon: 'edit',
		canCloseDialog: true,
		reference: AgentHubActions.EDIT,
		textClass: 'fg-general-5 text-body-regular',
		isHidden: [AdStatus.PROPERTY_OCCUPIED].includes(ad.adStatus.status as AdStatus) || [AdStatus.EXPIRED].includes(ad.adStatus.status as AdStatus),
	},
	{
		name: 'View ad on web',
		link: ad?.adlink,
		icon: 'language',
		isExternal: true,
		canCloseDialog: false,
		reference: AgentHubActions.VIEW_AD_ON_WEB,
		textClass: 'fg-general-5 text-body-regular',
		isHidden: ![AdStatus.LIVE, AdStatus.PROPERTY_OCCUPIED].includes(ad.adStatus.status as AdStatus),
	},
	{
		name: 'Share Ad',
		icon: 'share',
		canCloseDialog: true,
		reference: AgentHubActions.SHARE_AD,
		textClass: 'fg-general-5 text-body-regular',
		isHidden: ![AdStatus.LIVE, AdStatus.PROPERTY_OCCUPIED].includes(ad.adStatus.status as AdStatus),
	},
	{
		name: 'See all activities',
		icon: 'open_in_new',
		isHidden: true,
		isExternal: true,
		canCloseDialog: false,
		reference: AgentHubActions.SEE_ACTIVITY,
		textClass: 'fg-general-5 text-body-regular',
	},

	{
		name: 'Unpublish Ad',
		icon: 'pause_circle_outline',
		canCloseDialog: false,
		reference: AgentHubActions.UNPUBLISH_AD,
		textClass: 'fg-general-5 text-body-regular',
		isHidden: ![AdStatus.LIVE, AdStatus.PROPERTY_OCCUPIED].includes(ad.adStatus.status as AdStatus),
	},
	{
		name: 'Publish Ad',
		icon: 'play_circle_outline',
		canCloseDialog: false,
		reference: AgentHubActions.PUBLISH_AD,
		textClass: 'fg-general-5 text-body-regular',
		isHidden: ad.adStatus.status !== AdStatus.UNPUBLISH,
	},

	{
		name: 'Mark as Occupied',
		icon: 'check_box',
		canCloseDialog: false,
		reference: AgentHubActions.MARK_AS_OCCUPIED,
		textClass: 'fg-general-5 text-body-regular',
		isHidden:
			!(['properties-for-rent'].includes(ad.section.reference) && ad.adStatus.status !== AdStatus.PROPERTY_OCCUPIED) ||
			[AdStatus.EXPIRED].includes(ad.adStatus.status as AdStatus),
	},

	{
		name: 'Mark as Available',
		icon: 'timer',
		canCloseDialog: false,
		reference: AgentHubActions.MARK_AS_AVAILABLE,
		textClass: 'fg-general-5 text-body-regular',
		isHidden: !(['properties-for-rent'].includes(ad.section.reference) && ad.adStatus.status === AdStatus.PROPERTY_OCCUPIED),
	},

	{
		name: 'Mark as Sale Agreed',
		icon: 'check_box',
		canCloseDialog: false,
		reference: AgentHubActions.MARK_AS_SALE_AGREED,
		textClass: 'fg-general-5 text-body-regular',
		isHidden:
			['properties-for-rent'].includes(ad.section.reference) ||
			(['properties-for-sale'].includes(ad.section.reference) && ad.adStatus.status === AdStatus.SALE_AGREED) ||
			[AdStatus.EXPIRED].includes(ad.adStatus.status as AdStatus),
	},

	{
		name: 'Mark as Available',
		icon: 'timer',
		canCloseDialog: false,
		reference: AgentHubActions.MARK_AS_AVAILABLE,
		textClass: 'fg-general-5 text-body-regular',
		isHidden:
			['properties-for-rent'].includes(ad.section.reference) ||
			(['properties-for-sale'].includes(ad.section.reference) && ad.adStatus.status !== AdStatus.SALE_AGREED),
	},

	{
		name: 'Mark as Sold',
		icon: 'sell',
		canCloseDialog: false,
		reference: AgentHubActions.MARK_AS_SOLD,
		textClass: 'fg-general-5 text-body-regular',
		isHidden:
			['properties-for-rent'].includes(ad.section.reference) ||
			(['properties-for-sale'].includes(ad.section.reference) && ad.adStatus.status === AdStatus.SOLD) ||
			[AdStatus.EXPIRED].includes(ad.adStatus.status as AdStatus),
	},

	{
		name: 'Delete Ad',
		icon: 'archive',
		canCloseDialog: false,
		reference: AgentHubActions.DELETE_AD,
		textClass: 'fg-error-3 text-body-regular',
	},
];

export const ENQUIRY_DETAIL_ACTIONS = (enquiry: Inquiry) => [
	{
		name: 'View tenancy resume',
		icon: 'open_in_new',
		link: enquiry?.tenantin?.tenantResumeUrl,
		isHidden: !enquiry?.tenantin?.tenantResumeUrl,
		isExternal: true,
		canCloseDialog: false,
		reference: AgentHubActions.VIEW_TENANCY_RESUME,
		textClass: 'fg-general-5 text-body-regular',
	},
	{
		name: 'View ad on web',
		link: enquiry?.ad?.adlink,
		icon: 'language',
		isExternal: true,
		canCloseDialog: false,
		reference: AgentHubActions.VIEW_AD_ON_WEB,
		textClass: 'fg-general-5 text-body-regular',
	},
	{
		name: 'Mark as pending',
		icon: 'timer',
		isExternal: false,
		canCloseDialog: false,
		reference: AgentHubActions.MARK_AS_PENDING,
		isHidden: enquiry.completionStatus === EnquiryStatus.PENDING,
		value: EnquiryStatus.PENDING,
		textClass: 'fg-general-5 text-body-regular',
	},

	{
		name: 'Mark as completed',
		icon: 'check_box',
		isExternal: false,
		canCloseDialog: false,
		reference: AgentHubActions.MARK_AS_COMPLETED,
		isHidden: enquiry.completionStatus === EnquiryStatus.COMPLETED,
		value: EnquiryStatus.COMPLETED,
		textClass: 'fg-general-5 text-body-regular',
	},
];
