import { Component, EventEmitter, Input, Output } from '@angular/core';
import { Ad, AgentHubAction } from '@core/models';

@Component({
    selector: 'findqo-ad-overview',
    templateUrl: './ad-overview.component.html',
    styleUrl: './ad-overview.component.scss',
    standalone: false
})
export class AdOverviewComponent {
	@Input() ad: Ad;
	@Output() click = new EventEmitter();
	@Input() set options(data: AgentHubAction[]) {
		this.item = data.find((item) => item.name === 'Renew Ad');
	}
	dialogRef = null;
	@Input() isLoading = false;
	@Input() loaderRef = null;
	item: AgentHubAction;

	handleClick(item, ref): void {
		if (ref) {
			ref?.dismiss(item);
		} else {
			this.click.emit(item);
		}
	}
}
