import { Component, EventEmitter, Input, Output } from '@angular/core';
import { APP_ROUTE } from '@core/constants';
import { Ad, AgentHubAction } from '@core/models';
@Component({
    selector: 'findqo-actions',
    templateUrl: './actions.component.html',
    styleUrl: './actions.component.scss',
    standalone: false
})
export class ActionsComponent {
	constructor() {}

	@Input() ad: Ad;
	@Input() options: AgentHubAction[];
	@Input() isLoading = false;
	@Input() loaderRef = null;
	@Output() click = new EventEmitter();

	isBottomActionbar = false;
	readonly appRoute = APP_ROUTE;

	dialogRef = null;

	handleClick(item, ref): void {
		if (ref) {
			ref?.dismiss(item);
		} else {
			this.click.emit(item);
		}
	}
}
